function priceTitle(price) {
  let price_str = price.toFixed(9).toString();
  /*let price_str = indexTrim(price_str.replace("0.", ""), '0');
  if(price_str.length > 3) {
    price_str = price_str.substring(0, 3);
  }*/
  window.document.title = `${price_str} | $BONFIRE`;
}

function formatWallets(wallets) {
  return wallets.join(',')
}

function getCookie(name) {
  var dc = document.cookie;
  var prefix = name + "=";
  var begin = dc.indexOf("; " + prefix);
  if (begin === -1) {
    begin = dc.indexOf(prefix);
    if (begin !== 0) return null;
  } else {
    begin += 2;
  }
  var end = document.cookie.indexOf(";", begin);
  if (end === -1) {
    end = dc.length;
  }
  return unescape(dc.substring(begin + prefix.length, end));
}

function saveWallets(wallets) {
  const json = JSON.stringify(wallets);
  document.cookie = `data=${btoa(json)}; expires=Tue, 19 Jan 2038 03:14:07 UTC;`;
}

function loadWallets() {
  try {
    const data = atob(getCookie("data"));
    let arr = JSON.parse(data);
    if (Object.prototype.toString.call(arr) === '[object Array]') {
      arr = arr.filter(x => !!x);
      return arr;
    }
  } catch (e) { }
  return [];
}

const styles = theme => ({
  root: {
    top: 0,
    left: 0,
    minWidth: "100%",
    minHeight: "100%",
    background: "#222"
  },
  paper: {
    position: "relative",
    margin: "auto",
    [theme.breakpoints.down("sm")]: {
      paddingTop: "5px"
    },
    [theme.breakpoints.up("sm")]: {
      padding: "5px"
    },
    [theme.breakpoints.up("md")]: {
      padding: "10px"
    },
    [theme.breakpoints.up("lg")]: {
      padding: "20px"
    },
    minHeight: "70vh",
    width: "60vw",
    minWidth: "350px",
    border: "2px solid #a0ffe5",
    boxShadow: "inset 0 0 15px 0 #a0ffe5, 0 0 15px 0 #a0ffe5",
    background: "#222",
    outline: "none",
    transform: "translateY(+8%)",
  },
  donation_box: {
    paddingTop: "10vh",
  },
  donation_label: {
    color: '#01EFAF',
  },
  donation_address: {
    color: '#01EFAF',
    [theme.breakpoints.down("sm")]: {
      fontSize: "10pt"
    },
    [theme.breakpoints.up("sm")]: {
      fontSize: "12pt"
    },
  }
});

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      wasm: null,
      price_usd: null,
      price_bnb: null,
      balance: null,
      burnt: null,
      wallets: loadWallets(),
      error: null
    };
  }

  handleConnectionError = e => {
    alert(e);
    //window.location.reload();
  }

  clearError = () => {
    this.setState({ ...this.state, error: null })
  }

  loadWasm = () => {
    import("bonfire-tracker")
      .then(wasm => {
        wasm.init("https://bsc-dataseed.binance.org")
          .then(() => this.setState({ wasm }, () => {
            this.setState({ ...this.state, interval: setInterval(this.refresh, 3000) }, this.refresh);
          }))
          .catch(this.handleConnectionError);
      })
      .catch(this.handleConnectionError);
  }

  fetchPrice = () => {
    if (this.state.wasm) {
      this.state.wasm.get_price()
        .then(([price_bnb, price_usd]) => {
          priceTitle(price_usd);
          this.setState({ ...this.state, price_bnb, price_usd });
        })
        .catch(error => this.setState({ ...this.state, error }));
    }
  }

  fetchBalance = () => {
    if (this.state.wasm && this.state.wallets && this.state.wallets.length > 0) {
      this.state.wasm.get_balance(formatWallets(this.state.wallets))
        .then(balance => this.setState({ ...this.state, balance }))
        .catch(error => this.setState({ ...this.state, error }));
    }
  }

  fetchBurnt = () => {
    if (this.state.wasm) {
      this.state.wasm.get_balance("0x000000000000000000000000000000000000dead")
        .then(burnt => this.setState({ ...this.state, burnt }))
        .catch(error => this.setState({ ...this.state, error }));
    }
  }

  refresh = () => {
    this.fetchPrice();
    this.fetchBurnt();
    this.fetchBalance();
  }

  editWallet = (i, new_wallet) => {
    if (new_wallet) {
      new_wallet = new_wallet.trim().toLowerCase();
      if (this.state.wasm.check_address(new_wallet)) {
        let wallets = this.state.wallets;
        if ((i === null || !wallets[i]) && wallets.indexOf(new_wallet) === -1) {
          wallets.push(new_wallet);
        } else {
          wallets[i] = new_wallet;
        }
        this.setState({ ...this.state, wallets }, () => {
          saveWallets(wallets);
          this.fetchBalance();
        });
      } else {
        alert("Invalid wallet address");
      }
    }
  }

  deleteWallet = (i) => {
    let wallets = this.state.wallets;
    delete wallets[i];
    this.setState({ ...this.state, wallets }, () => {
      saveWallets(wallets);
      this.fetchBalance();
    });
  }

  render() {
    const { classes } = this.props;
    return (
      <Box className={classes.root}>
        <Paper className={classes.paper}>
          {this.state.error &&
            <ErrorDisplay error={this.state.error} clearError={this.clearError} />
          }
          {this.state.wasm !== null && this.state.price_usd !== null ?
            <MainView {...this.state} editWallet={this.editWallet} deleteWallet={this.deleteWallet} /> :
            <Loading />
          }
        </Paper>
        <Box className={classes.donation_box}>
          <center>
            <Typography className={classes.donation_label}>Donate some <img width="16" height="16" src="./fire.svg" alt="bonfire.svg" />:</Typography>
            <Typography className={classes.donation_address}>0x37A9C69F952E033e11330B7E2F1dDf76a2033Cf7</Typography>
          </center>
        </Box>
      </Box>
    );
  }

  componentDidMount = () => {
    this.loadWasm();
  }
}

export default withStyles(styles)(App);
