import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Box, Typography, IconButton, Tooltip } from '@material-ui/core';
import ClearIcon from '@material-ui/icons/Clear';
import AddIcon from '@material-ui/icons/Add';
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';

const useStyles = makeStyles(theme => ({
    scaled_font: {
        [theme.breakpoints.down("sm")]: {
            fontSize: "8.5pt",
        },
        [theme.breakpoints.up("sm")]: {
            fontSize: "8.7pt",
        },
        [theme.breakpoints.up("md")]: {
            fontSize: "12pt",
        },
        [theme.breakpoints.up("lg")]: {
            fontSize: "13pt",
        },
    },
    label: {
        color: '#01EFAF',
        fontFamily: "Arial",
        paddingTop: "10px",
        [theme.breakpoints.down("sm")]: {
            fontSize: "10pt",
            paddingLeft: "15px",
            paddingRight: "35px"
        },
        [theme.breakpoints.up("sm")]: {
            fontSize: "11pt",
            paddingLeft: "25px",
            paddingRight: "35px"
        },
        [theme.breakpoints.up("md")]: {
            fontSize: "12pt",
            paddingLeft: "25px",
            paddingRight: "35px"
        },
        [theme.breakpoints.up("lg")]: {
            fontSize: "13pt",
            paddingLeft: "30px",
            paddingRight: "40px"
        },
        textAlign: "left",
    },
    hr: {
        border: 0,
        boxShadow: "inset 0 0 10px 1px #a0ffe5, 0 0 10px 1px #a0ffe5",
        height: "1px",
        background: "-webkit-gradient(linear, 0 0, 100% 0, from(transparent), to(transparent), color-stop(50%, #bbbbbb))"
    },
    error_box: {
        padding: "2px",
        fontFamily: "Arial",
        border: "1px solid grey"
    },
    error_title: {
        fontSize: "12pt",
        padding: 0,
        margin: 0
    },
    help_icon: {
        width: "15px",
        height: "15px"
    },
    tooltip_text: {

    },
    btn: {
        color: "green"
    }
}));

function BnbHelpText() {
    const classes = useStyles();
    return <Typography className={classes.tooltip_text}>
        PancakeSwap uses constant product formula for price calculation which is basically ratio between sizes of two liquidity pools with main liquidity pool in BNB
        so it might be useful to consider BNB price as real indicator of token performance since token USD price is proportional to BNB price.
    </Typography>;
}

function TableDisplay(props) {
    const { label, value } = props;
    return <p style={{ textAlign: "left" }}>{label}<span style={{ float: "right" }}>{value}</span></p>;
}

function WalletDisplay(props) {
    const classes = useStyles();
    const { wallets, deleteWallet } = props;
    if (!wallets || wallets.length === 0) {
        return null;
    }
    const rows = wallets.map((w, i) => <Typography key={i} className={classes.scaled_font}>{w}
        <IconButton size="small" onClick={() => deleteWallet(i)} style={{ color: '#a50156' }}><ClearIcon /></IconButton>
    </Typography>);
    return (
        <Box style={{ textAlign: "center" }}>
            {rows}
        </Box>
    )
}

export function MainView(props) {
    const [open, setOpen] = React.useState(false);
    const supply = 1000000000000000;
    const classes = useStyles();
    const { price_bnb, price_usd, balance, burnt, wallets, editWallet, deleteWallet } = props;
    const mcap = price_usd * (supply - burnt);
    const burnt_percent = (burnt / (supply / 100)).toFixed(2);
    const wallets_active = wallets !== null && wallets.length > 0;
    return (
        <Box>
            <Box className={classes.dataDisplay}>
                <Box className={classes.label}><center>Live $BONFIRE stats directly from blockchain</center></Box>
                <hr className={`${classes.hr}`} />
                <Box className={classes.label}><TableDisplay label="Price:" value={price_usd.toFixed(10)} /></Box>
                <hr className={`${classes.hr}`} />
                <Box className={classes.label}>
                    <TableDisplay
                        label={
                            <React.Fragment>
                                <span>Price BNB: </span>
                                <Tooltip title={<BnbHelpText />} open={open} disableTouchListener onClose={() => setOpen(false)}>
                                    <IconButton className={classes.help_icon} onClick={() => setOpen(!open)}>
                                        <HelpOutlineIcon className={`${classes.help_icon} ${classes.btn}`} />
                                    </IconButton>
                                </Tooltip>
                            </React.Fragment>
                        }
                        value={price_bnb.toFixed(13)}
                    />
                </Box>
                <hr className={`${classes.hr}`} />
                <Box className={classes.label}><TableDisplay label="Marketcap:" value={`$${Number(Math.round(mcap)).toLocaleString()}`} /></Box>
                <hr className={`${classes.hr}`} />
                <Box className={classes.label}><TableDisplay label="Burned:" value={`${burnt} (${burnt_percent}%)`} /></Box>
                <hr className={`${classes.hr}`} />
                <Box className={classes.label}>
                    <p>Wallets:<IconButton
                        onClick={() => {
                            editWallet(null, prompt("Enter wallet address:"));
                        }}
                    >
                        <AddIcon className={classes.btn} />
                    </IconButton>
                    </p>
                    <WalletDisplay wallets={wallets} deleteWallet={deleteWallet} />
                </Box>
                {wallets_active &&
                    <div>
                        <hr className={`${classes.hr}`} />
                        <Box className={classes.label}><TableDisplay label="$BONFIRE balance:" value={balance} /></Box>
                        <hr className={`${classes.hr}`} />
                        <Box className={classes.label}><TableDisplay label="$USD balance:" value={(balance * price_usd).toFixed(3)} /></Box>
                        <hr className={`${classes.hr}`} />
                        <Box className={classes.label}><TableDisplay label="$BNB balance:" value={(balance * price_bnb).toFixed(3)} /></Box>
                    </div>
                }
            </Box>
        </Box>
    );
}

export function ErrorDisplay(props) {
    const { error, clearError } = props;
    const classes = useStyles();
    return (
        <Box className={classes.error_box}>
            <center><Box className={classes.error_title}>Error<IconButton size="small" onClick={clearError}><ClearIcon /></IconButton></Box></center>
            <Typography>{error}</Typography>
        </Box>
    );
}
